import React from 'react';
import { 
  Heading, 
  Paragraph, 
  Card, 
  Tag 
} from '@digdir/designsystemet-react';

interface SlackChannel {
  name: string;
  purpose: string;
  category: string;
  isPrivate?: boolean;
  isSubChannel?: boolean;
}

const SlackChannels: React.FC = () => {
  const channels: SlackChannel[] = [
    {
      name: 'designsystemet',
      purpose: 'Hovedkanal for Designsystemet - generelle diskusjoner og spørsmål',
      category: 'Hovedkanal'
    },
    {
      name: 'mønstre-og-god-praksis',
      purpose: 'Diskusjoner om designmønstre og beste praksis',
      category: 'Spesialisert'
    },
    {
      name: 'random',
      purpose: 'Uformelle diskusjoner og deling av interessant innhold',
      category: 'Generell'
    },
    {
      name: 'sparring',
      purpose: 'Få tilbakemeldinger og diskuter designløsninger',
      category: 'Support'
    },
    {
      name: 'team-designsystemet',
      purpose: 'Intern kanal for Designsystem-teamet',
      category: 'Team',
      isPrivate: true
    },
    {
      name: 'design-ops',
      purpose: 'DesignOps-relaterte diskusjoner og ressurser',
      category: 'Spesialisert',
      isSubChannel: true
    },
    {
      name: 'design-show-and-tell',
      purpose: 'Del og vis frem designarbeid og prosjekter',
      category: 'Spesialisert',
      isSubChannel: true
    },
    {
      name: 'felles-innsiktsbibliotek',
      purpose: 'Deling og diskusjon av brukerinnsikt',
      category: 'Spesialisert',
      isSubChannel: true
    }
  ];

  const getCategoryColor = (category: string): 'success' | 'warning' | 'info' | 'neutral' => {
    switch (category.toLowerCase()) {
      case 'hovedkanal':
        return 'info';     // Main channel - info color to indicate main information hub
      case 'spesialisert':
        return 'warning';  // Specialized - warning color to make specialized channels stand out
      case 'generell':
        return 'neutral';  // General - neutral color for general discussions
      case 'support':
        return 'success';  // Support - success color for help/support
      case 'team':
        return 'warning';  // Team - warning color to highlight private team channels
      default:
        return 'neutral';
    }
  };

  const getChannelPrefix = (channel: SlackChannel): string => {
    if (channel.isPrivate) return '🔒';
    if (channel.isSubChannel) return '+';
    return '#';
  };

  return (
    <div>
      <Heading level={1} data-size="2xl" style={{ marginBottom: '1rem' }}>
        Slack-kanaler
      </Heading>
      <div style={{ marginBottom: '1rem' }}>
        <Paragraph>
          Finn relevante Slack-kanaler for Designsystemet-diskusjoner og støtte.
        </Paragraph>
      </div>

      <Card>
        <div style={{ padding: '1rem' }}>
          <table style={{ width: '100%', borderCollapse: 'collapse' }}>
            <thead>
              <tr>
                <th style={{ textAlign: 'left', padding: '0.5rem' }}>Kanal</th>
                <th style={{ textAlign: 'left', padding: '0.5rem' }}>Beskrivelse</th>
                <th style={{ textAlign: 'left', padding: '0.5rem' }}>Type</th>
              </tr>
            </thead>
            <tbody>
              {channels.map((channel) => (
                <tr key={channel.name}>
                  <td style={{ padding: '0.5rem' }}>
                    {getChannelPrefix(channel)}{channel.name}
                  </td>
                  <td style={{ padding: '0.5rem' }}>{channel.purpose}</td>
                  <td style={{ padding: '0.5rem' }}>
                    <Tag data-size="sm" data-color={getCategoryColor(channel.category)}>
                      {channel.category}
                    </Tag>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </Card>

      <div style={{ marginTop: '2rem' }}>
        <Heading level={2} data-size="lg" style={{ marginBottom: '1rem' }}>
          Kanalforklaring
        </Heading>
        <Card>
          <div style={{ padding: '1rem' }}>
            <ul style={{ listStyle: 'none', padding: 0 }}>
              <li style={{ marginBottom: '1rem' }}>
                <span style={{ marginRight: '0.5rem' }}>#</span>
                <Tag data-size="sm" data-color="success">Offentlig kanal</Tag>
                <div style={{ marginTop: '0.5rem' }}>
                  <Paragraph>Åpne kanaler for alle.</Paragraph>
                </div>
              </li>
              <li style={{ marginBottom: '1rem' }}>
                <span style={{ marginRight: '0.5rem' }}>🔒</span>
                <Tag data-size="sm" data-color="warning">Privat kanal</Tag>
                <div style={{ marginTop: '0.5rem' }}>
                  <Paragraph>Kun for inviterte medlemmer.</Paragraph>
                </div>
              </li>
              <li style={{ marginBottom: '1rem' }}>
                <span style={{ marginRight: '0.5rem' }}>+</span>
                <Tag data-size="sm" data-color="info">Underkanal</Tag>
                <div style={{ marginTop: '0.5rem' }}>
                  <Paragraph>Spesialiserte temakanaler.</Paragraph>
                </div>
              </li>
            </ul>
          </div>
        </Card>
      </div>
    </div>
  );
};

export default SlackChannels;
