import React from 'react';
import { 
  Paragraph, 
  Link, 
  Tag, 
  Spinner,
  Card,
  Heading,
  Divider
} from '@digdir/designsystemet-react';

interface SearchResult {
  title: string;
  summary: string;
  source: string;
  url: string;
}

interface SearchResultsProps {
  results: SearchResult[];
  isLoading: boolean;
}

const SearchResults: React.FC<SearchResultsProps> = ({ results, isLoading }) => {
  if (isLoading) {
    return (
      <div style={{ textAlign: 'center', padding: '32px' }}>
        <div aria-label="Searching...">
          <Spinner
            data-size="md"
            data-variant="interaction"
            aria-hidden="true"
          />
        </div>
      </div>
    );
  }

  if (results.length === 0) {
    return null;
  }

  return (
    <div style={{ marginTop: '24px' }}>
      <div style={{ marginBottom: '1rem' }}>
        <Heading level={2} data-size="sm">
          Search Results ({results.length})
        </Heading>
      </div>
      
      <div style={{ marginTop: '16px' }}>
        {results.map((result, index) => (
          <div key={index} style={{ marginBottom: index < results.length - 1 ? '16px' : 0 }}>
            <Card>
              <div style={{ padding: '16px' }}>
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '8px' }}>
                  <Link href={result.url}>
                    <div style={{ marginBottom: '0.5rem' }}>
                      <Heading level={3} data-size="xs">
                        {result.title}
                      </Heading>
                    </div>
                  </Link>
                  <Tag 
                    data-size="sm" 
                    data-color={
                      result.source === 'FAQ' ? 'success' :
                      result.source === 'Slack' ? 'info' :
                      'neutral'
                    }
                  >
                    {result.source}
                  </Tag>
                </div>
                
                <Divider color="subtle" />
                
                <div style={{ marginTop: '12px' }}>
                  <Paragraph data-size="sm">
                    {result.summary}
                  </Paragraph>
                </div>
              </div>
            </Card>
          </div>
        ))}
      </div>
    </div>
  );
};

export default SearchResults;
